<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('PURPOSE_NAME') }}</label>
          <vs-input class="w-full" v-model="transferPurpose.purposeName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('PARENT_PURPOSE') }}</label>
          <funds-transfer-purpose-lookup :title="$t('PARENT_PURPOSE')" :recordValue="transferPurpose.parentPurposeId" :recordDisplay="transferPurpose.parentPurposeName" valueField="purposeId" displayField="purposeName" @select-record="selectTransferPurposeRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TRANSFER_TYPE') }}</label>
          <funds-transfer-type-lookup :title="$t('TRANSFER_TYPE')" :recordValue="transferPurpose.transferTypeId" :recordDisplay="transferPurpose.transferTypeName" valueField="typeId" displayField="typeName" @select-record="selectTransferTypeRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TRANSFER_CURRENCY') }}</label>
          <currency-lookup :title="$t('TRANSFER_CURRENCY')" :recordValue="transferPurpose.transferCurrencyId" :recordDisplay="transferPurpose.transferCurrencyDescription" valueField="uomId" displayField="description" @select-record="selectTransferCurrencyRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
          <vs-input class="w-full" v-model="transferPurpose.externalPurposeId"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('POSITION') }}</label>
          <div class="flex">
            <vs-input-number v-model="transferPurpose.position" min="0"/>
          </div>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyTransferPurpose('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyTransferPurpose('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyTransferPurpose('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import FundsTransferPurposeService from '@/services/ft/FundsTransferPurposeService'
import FundsTransferPurposeLookup from '@/views/ft/funds-transfer-purpose/FundsTransferPurposeLookup'
import FundsTransferTypeLookup from '@/views/ft/funds-transfer-type/FundsTransferTypeLookup'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'

export default {
  name: 'funds-transfer-purpose-modify',
  mixins: [AlertMixin],
  components: {
    FundsTransferPurposeLookup,
    FundsTransferTypeLookup,
    CurrencyLookup
  },
  data: function () {
    return {
      transferPurpose: {}
    }
  },
  methods: {
    async load () {
      let self = this
      FundsTransferPurposeService.transferPurposes({
        purposeId: this.$route.params.id
      }).then(function (response) {
        self.transferPurpose = response.data.transferPurposeList[0] || {}
      })
    },
    selectTransferTypeRecord (record) {
      this.transferPurpose.transferTypeId = record.typeId || ''
    },
    selectTransferCurrencyRecord (record) {
      this.transferPurpose.transferCurrencyId = record.uomId || ''
    },
    selectTransferPurposeRecord (record) {
      this.transferPurpose.parentPurposeId = record.purposeId || ''
    },
    modifyTransferPurpose (nextAction) {
      let self = this
      FundsTransferPurposeService.modifyTransferPurpose({
        purposeId: this.$route.params.id,
        parentPurposeId: this.transferPurpose.parentPurposeId,
        externalPurposeId: this.transferPurpose.externalPurposeId,
        purposeName: this.transferPurpose.purposeName,
        transferTypeId: this.transferPurpose.transferTypeId,
        transferCurrencyId: this.transferPurpose.transferCurrencyId,
        position: this.transferPurpose.position
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewFundsTransferPurpose', params: { id: response.data.purposeId } })
          } else {
            self.$router.push({ name: 'BrowseFundsTransferPurposes' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
